import { Component, Element, Event, EventEmitter, getAssetPath, h, Host, JSX, Prop, State } from '@stencil/core';
import { Series } from '../../entities/content';
import { getModelsRoutingKey } from '../../entities/routing-key';
import { navContentStore } from '../../state/nav-content-store';
import { navStateStore } from '../../state/nav-state-store';
import { EventActions, MenuItemVariant } from '../../utility/constants';
import { getConfig, getImageSize } from '../../utility/helper';

@Component({
  tag: 'phn-models',
  styleUrl: 'models.scss',
  assetsDirs: ['series-assets'],
  scoped: true
})
export class Models {
  @Prop() breakpoint!: number;
  @Prop() showHeadline = false;
  @State() showSkeleton = true;

  @Element() el!: HTMLPhnModelsElement;

  private seriesImagesLoaded = new Set<string>();
  private seriesImagesNotLoaded = new Set<string>();

  @Event({ bubbles: true }) mobileMenuItemClick!: EventEmitter<string[]>;

  private showMenuWithModels(series: Series): void {
    const modelsSeriesRoutingKey = getModelsRoutingKey(series.id);
    this.mobileMenuItemClick.emit([modelsSeriesRoutingKey]);
  }

  private handleImageLoad(seriesId: string) {
    const { models } = navContentStore.state;

    this.seriesImagesLoaded.add(seriesId);

    if (models.series.every(({ id }) => this.seriesImagesLoaded.has(id))) {
      this.showSkeleton = false;
    }
  }

  private handleImageLoadError(seriesId: string) {
    this.handleImageLoad(seriesId);
    this.seriesImagesNotLoaded.add(seriesId);
  }

  private getImageSource(image: string): string {
    const assetPath = getAssetPath(`${getConfig(navStateStore.state.env).ASSETS_URL}/series-assets`);
    const imageSize = getImageSize(this.breakpoint);
    const imagePath = `${assetPath}/${imageSize}`;

    return `${imagePath}/${image}.webp, ${imagePath}/${image}@2x.webp 2x`;
  }

  render(): JSX.Element {
    const { models } = navContentStore.state;

    return (
      <Host>
        <ul>
          {models.series.map((modelsSeries) => (
            <phn-menu-item
              key={modelsSeries.id}
              class={`series ${this.seriesImagesNotLoaded.has(modelsSeries.id) || this.showSkeleton ? 'skeleton' : ''}`}
              elementId={getModelsRoutingKey(modelsSeries.id)}
              analyticsId={modelsSeries.id}
              text={modelsSeries.name}
              subtext={modelsSeries.tags.map((tag) => tag.text).join(' | ')}
              onClick={() => this.showMenuWithModels(modelsSeries)}
              variant={MenuItemVariant.Model}
              isModels={true}
              datalayerEventAction={EventActions.MODEL_RANGE_CLICK}
              role="listitem"
            >
              <img
                slot="media"
                class="image"
                srcSet={this.getImageSource(modelsSeries.image)}
                alt={`${modelsSeries.name} Frontshot`}
                onLoad={() => this.handleImageLoad(modelsSeries.id)}
                onError={() => this.handleImageLoadError(modelsSeries.id)}
              />
            </phn-menu-item>
          ))}
        </ul>
      </Host>
    );
  }
}
